
import { typeOptions } from "@front/components/danmakuFlow/utils/data";
import { defineComponent } from "vue";
export default defineComponent({
	props: {
		settings: {
			required: true
		},
		mockData: {
			default: () => {
				return [];
			}
		}
	},
	computed: {
		typeOptions,
		setting: {
			get(): any {
				return this.settings;
			},
			set(e: any) {
				this.$emit("update:settings", e);
			}
		}
	}
});
